@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes GradientAnimation {
  0%{background-position:47% 0%}
  50%{background-position:50% 40%}
  100%{background-position:47% 0%}
}

.html {
  height: 100%;
}

body, html, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none!important;
}

body{
  font-family: Arial, Helvetica, sans-serif;
  /* background-image: linear-gradient(to bottom, #000000, #7c0e0e); */
  background: url('https://raw.githubusercontent.com/MonolithOfficial/darkwirejsons/master/images/monolithicbg.jpg');
  background-size: 400% 400%;
  animation: GradientAnimation 15s ease infinite;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;

}

#navbar {
  font-size:1.3rem;
  padding-top: 1.1rem;
  padding-bottom: 1.1rem;
  position: relative;
  display: flex;
  align-items: center;
  background-color: #190061;
  width: 90%;
  height: auto;
  margin: auto;
  border: 5px solid #282828;

  /* border-radius: 2rem; */
}

#mainLogo {
  display: block;
  margin: auto;
  padding: 1.2rem;
  padding-left: 0;
  padding-right: 0;
  max-width: 100%;
}

#navbar ul {
  margin-top: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  list-style: none;
  width: 45vw;
  margin: auto;
}

#navbar ul li {
  margin: 10%;
}
#navbar ul li a {
  text-decoration: none;
  color: white;
  font-weight: lighter;
}
#navbar ul li a:hover {
  color: grey;
}

#topContainer {
  background-image: linear-gradient(to top, #000000, #0c0032);
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: 8fr 2fr;
  align-items: center;
  justify-items: flex-start;
  column-gap: 1.2rem;
  margin-top: 1.2rem;
}



.slideshow-container {
  max-width: 100%;
  position: relative;
  padding: 1.2rem;
}

.mySlides {
  position: relative;
  display: none;
}
.mySlides img {
  filter: brightness(100%);
  transition: 0.3s;
}

.mySlides img:hover{
  filter: brightness(60%);
}

.prev, .next {
  cursor: pointer;
  background-color: black;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

.next {
  
  right: 0;
  border-radius: 3px 0 0 3px;
}

.prev:hover, .next:hover {
  background-color: rgba(150, 12, 12, 0.8);
}

.text {
  background-color: black;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
  color: #f2f2f2;
  font-size: 15px;
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
}

.numbertext {
  background-color: black;
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px;
  top: 0;
  left: 0;
  /* width: 100%; */
  z-index: 1;
  text-align: center;
  position: absolute;
  top: 0;
}

.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

#pageWrapper {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-image: linear-gradient(to top, #000000, #0c0032);
  width: 70%;
  margin: auto;
  margin-top: 20px;
  border: 5px solid #282828;
  /* border-radius: 20px; */
}

#pageWrapperHome {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  display: grid;
  grid-template-columns: 4fr 2fr;
  
  width: 80%;
  margin: auto;
  margin-top: 20px;
}

.wrapper {
  margin: 0;
  width: 95%;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 1fr;
}

.articleDiv {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 2fr; 
  grid-auto-rows: minmax(min-content, max-content);
  align-items: flex-start;
  background-image: linear-gradient(to top, #000000, #0c0032);
  padding: 0.5rem;
  /* border-radius: 0.5rem; */
  transition: 0.2s;
}

.charts {
  background-image: linear-gradient(to top, #000000, #0c0032);
  width: 100%;
  height: 80%;
  /* border-radius: 1.2rem; */
}

.formSpan {
  display: block;
  width: 100%;
  cursor: pointer;
}

.articleDiv:hover {
  transform: scale(1.02);
}

.articleDiv img {
  max-width: 100%;
}
.articleCategory {
  color: grey;
  margin: 0.5rem;
}
.articleTitle {
  color: white;
  margin: 0.5rem;
}
.subBtn {
  display: none;
  
}

.desc {
  width: 60vw;
  text-align: start;
  color: white;
  margin: auto;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.mainSubTitle{
  color: white;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  text-align: center;
}

.wrapperArticle img {
  display: block;
  margin: auto;
  width: 45vw;
  border-radius: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  transition: 0.5s;
}
.wrapperArticle img:hover {
  transform: scale(1.05);
}

#footer {
  width: 100%;
  background-color: white;
  height: 20rem;
  margin-top: 1.2rem;
  background: linear-gradient(to bottom, #000000, #0c0032);
  border-top: 5px solid grey;
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
}

#footer p {
  color: #9a9a9a;
}

#footerContent {
  width: 90%;
  height: 15rem;
  margin: auto;
  border-bottom: 1px solid grey;

}

#footerCopyright {
  
}


@media only screen and (max-width: 1200px){
  #pageWrapperHome {
    width: 100%;
    grid-template-columns: 1fr;
  }
  .wrapper {
      grid-template-columns: repeat(3, 1fr);
      
      width: 90%;
      margin: auto;
  }
  .articleDiv {
    width: 95%;
    grid-template-columns: 1fr; 
  }
  .slideshow-container {
      max-width: 100%;
  }
}

@media only screen and (max-width: 1200px){
  #topContainer {
      grid-template-columns: 1fr;
  }
  .slideshow-container {
      margin: auto;
  }
  .charts {
      display: none;
  }
}

@media only screen and (max-width: 768px){
  #topContainer {
      width: 100%;
  }
  .slideshow-container {
      width: 100%;
      padding: 0;
  }
  #pageWrapper {
      width: 100%;
      border: hidden;
      border-radius: 0;
  }
  .wrapper {
      grid-template-columns: repeat(2, 1fr);
  }
  .mainSubTitle {
      margin-top: 0;
  }
  .desc {
      width: 95vw;
  }
  .wrapperArticle img {
      width: 95vw;
      padding-bottom: 0;
  }
  .wrapperArticle img:hover {
      transform: none;
  }
}
@media only screen and (max-width: 500px){
  body {
    overflow-x: hidden;
  }
  .wrapper {
      grid-template-columns: repeat(1, 1fr);
  }
  #navbar {
    width: 97%;
  }
  
}


